import "./styles.css";
import * as PIXI from "pixi.js";
import { DropShadowFilter } from "@pixi/filter-drop-shadow";

// Function to check if the device is in portrait mode
function isPortrait() {
  return window.innerHeight > window.innerWidth;
}

// Define variables that will change based on orientation
let REEL_WIDTH;
let SYMBOL_SIZE;
const SYMBOLS_PER_REEL = 4;
let UI_AREA_HEIGHT;

// Adjust sizes based on orientation
if (isPortrait()) {
  // Portrait mode sizes
  REEL_WIDTH = 160;
  SYMBOL_SIZE = 120;
  UI_AREA_HEIGHT = 100;
} else {
  // Landscape mode sizes
  REEL_WIDTH = 240;
  SYMBOL_SIZE = 200;
  UI_AREA_HEIGHT = 80;
}

// Create PIXI application
const app = new PIXI.Application({
  width: isPortrait() ? window.innerWidth : REEL_WIDTH * 6,
  height: isPortrait()
    ? window.innerHeight
    : SYMBOL_SIZE * SYMBOLS_PER_REEL + UI_AREA_HEIGHT,
  transparent: true,
});

// Append the view to the DOM
const gameContainer = document.querySelector(".game-container");
gameContainer.appendChild(app.view);

// Load assets
PIXI.Loader.shared
  .add("fruits", "assets/fruit.json")
  .add("reels", "assets/reel.json")
  .add("ui", "assets/UI_2.json")
  .load(onAssetsLoaded)
  .onError.add((error) => {
    console.error("Error loading asset:", error.message);
  });

function onAssetsLoaded() {
  const slotTextures = [
    PIXI.Texture.from("assets/1.png"),
    PIXI.Texture.from("assets/2.png"),
    PIXI.Texture.from("assets/3.png"),
    PIXI.Texture.from("assets/4.png"),
    PIXI.Texture.from("assets/5.png"),
    PIXI.Texture.from("assets/6.png"),
    PIXI.Texture.from("assets/7.png"),
    PIXI.Texture.from("assets/8.png"),
  ];

  const reelTexture1 = PIXI.Texture.from("assets/reel1.png");
  const reelTexture2 = PIXI.Texture.from("assets/reel2.png");

  // Create reel background
  const reelBackground = new PIXI.Sprite(reelTexture1);
  reelBackground.width = REEL_WIDTH * 5;
  reelBackground.height = SYMBOL_SIZE * SYMBOLS_PER_REEL;
  app.stage.addChild(reelBackground);

  reelBackground.x = isPortrait()
    ? (app.screen.width - reelBackground.width) / 2
    : 0;
  reelBackground.y = isPortrait() ? 50 : 0;

  // Create reel overlay
  const reelOverlay = new PIXI.Sprite(reelTexture2);
  reelOverlay.width = REEL_WIDTH * 5;
  reelOverlay.height = SYMBOL_SIZE * SYMBOLS_PER_REEL;
  app.stage.addChild(reelOverlay);

  reelOverlay.x = reelBackground.x;
  reelOverlay.y = reelBackground.y;

  // Create reels
  const reels = [];
  const reelContainer = new PIXI.Container();
  reelContainer.y = reelBackground.y;
  reelContainer.x = reelBackground.x;
  app.stage.addChild(reelContainer);

  for (let i = 0; i < 5; i++) {
    const rc = new PIXI.Container();
    rc.x = i * REEL_WIDTH;
    reelContainer.addChild(rc);

    const reel = {
      container: rc,
      symbols: [],
      position: 0,
      previousPosition: 0,
      blur: new PIXI.filters.BlurFilter(),
    };
    reel.blur.blurX = 0;
    reel.blur.blurY = 0;
    rc.filters = [reel.blur];

    for (let j = 0; j < SYMBOLS_PER_REEL + 1; j++) {
      const symbol = new PIXI.Sprite(
        slotTextures[Math.floor(Math.random() * slotTextures.length)]
      );
      symbol.y = j * SYMBOL_SIZE;
      symbol.scale.x = symbol.scale.y = Math.min(
        SYMBOL_SIZE / symbol.width,
        SYMBOL_SIZE / symbol.height
      );
      symbol.x = Math.round((REEL_WIDTH - symbol.width) / 2);

      reel.symbols.push(symbol);
      rc.addChild(symbol);
    }
    reels.push(reel);
  }

  // Create reel mask
  const reelMask = new PIXI.Graphics();
  reelMask.beginFill(0x000000);
  reelMask.drawRect(0, 0, REEL_WIDTH * 5, SYMBOL_SIZE * SYMBOLS_PER_REEL);
  reelMask.endFill();
  reelContainer.mask = reelMask;
  reelMask.x = reelBackground.x;
  reelMask.y = reelBackground.y;
  app.stage.addChild(reelMask);

  // UI Elements
  // Define textures
  const buttonAboveDefaultTexture = PIXI.Texture.from("assets/button_above_default.png");
  const buttonAboveHoverTexture = PIXI.Texture.from("assets/button_above_hover.png");
  const buttonAbovePressedTexture = PIXI.Texture.from("assets/button_above_pressed.png");
  const buttonAboveDarkerTexture = PIXI.Texture.from("assets/button_above_darker.png");

  const spinButtonDefaultTexture = PIXI.Texture.from("assets/spin_button_default.png");
  const spinButtonHoverTexture = PIXI.Texture.from("assets/spin_button_hover.png");
  const spinButtonPressedTexture = PIXI.Texture.from("assets/spin_button_pressed.png");
  const spinButtonStopDefaultTexture = PIXI.Texture.from("assets/spin_button_stop_default.png");
  const spinButtonStopHoverTexture = PIXI.Texture.from("assets/spin_button_stop_hover.png");
  const spinButtonStopPressedTexture = PIXI.Texture.from("assets/spin_button_stop_pressed.png");

  const buttonBelowDefaultTexture = PIXI.Texture.from("assets/button_below_default.png");
  const buttonBelowHoverTexture = PIXI.Texture.from("assets/button_below_hover.png");
  const buttonBelowPressedTexture = PIXI.Texture.from("assets/button_below_pressed.png");
  const buttonBelowTurboTexture = PIXI.Texture.from("assets/button_below_turbo.png");

  const settingsIconDefaultTexture = PIXI.Texture.from("assets/settings_default.png");
  const settingsIconHoverTexture = PIXI.Texture.from("assets/settings_hover.png");
  const settingsIconPressedTexture = PIXI.Texture.from("assets/settings_pressed.png");

  const musicIconTextures = [
    PIXI.Texture.from("assets/music_icon_0.png"),
    PIXI.Texture.from("assets/music_icon_1.png"),
    PIXI.Texture.from("assets/music_icon_2.png"),
  ];
  const musicIconHoverTextures = [
    PIXI.Texture.from("assets/music_icon_hover_0.png"),
    PIXI.Texture.from("assets/music_icon_hover_1.png"),
    PIXI.Texture.from("assets/music_icon_hover_2.png"),
  ];
  const musicIconPressedTextures = [
    PIXI.Texture.from("assets/music_icon_pressed_0.png"),
    PIXI.Texture.from("assets/music_icon_pressed_1.png"),
    PIXI.Texture.from("assets/music_icon_pressed_2.png"),
  ];

  const aIconDefaultTexture = PIXI.Texture.from("assets/a_icon_default.png");
  const boltIconDefaultTexture = PIXI.Texture.from("assets/bolt_icon_default.png");

  // Create icons
  const buttonAbove = new PIXI.Sprite(buttonAboveDefaultTexture);
  const spinButton = new PIXI.Sprite(spinButtonDefaultTexture);
  const buttonBelow = new PIXI.Sprite(buttonBelowDefaultTexture);
  const settingsIcon = new PIXI.Sprite(settingsIconDefaultTexture);
  const musicNoteIcon = new PIXI.Sprite(musicIconTextures[0]);
  const aIcon = new PIXI.Sprite(aIconDefaultTexture);
  const boltIcon = new PIXI.Sprite(boltIconDefaultTexture);

  const icons = [settingsIcon, boltIcon, spinButton, aIcon, musicNoteIcon];

  const BUTTON_SCALE = 1.0;
  const ICON_SCALE = isPortrait() ? 0.8 : 1.0;

  // Scale and anchor
  icons.forEach((icon) => {
    icon.scale.set(ICON_SCALE);
    icon.anchor.set(0.5);
  });

  buttonAbove.scale.set(BUTTON_SCALE);
  buttonAbove.anchor.set(0.5);
  buttonBelow.scale.set(BUTTON_SCALE);
  buttonBelow.anchor.set(0.5);

  // Position icons
  if (isPortrait()) {
    const iconSpacing = 20;
    const totalIconsWidth =
      icons.reduce((acc, icon) => acc + icon.width, 0) +
      iconSpacing * (icons.length - 1);
    let startX = (app.screen.width - totalIconsWidth) / 2;

    icons.forEach((icon) => {
      icon.x = startX + icon.width / 2;
      icon.y = app.screen.height - UI_AREA_HEIGHT - icon.height - 20;
      startX += icon.width + iconSpacing;
    });

    // Position balance, bet, win boxes at the bottom
    const boxWidth = app.screen.width / 3;
    const boxHeight = UI_AREA_HEIGHT;
    const boxBackgroundColor = 0x5a1d63;
    const boxBorderColor = 0x6a2d73;

    const balanceBox = new PIXI.Graphics();
    balanceBox.beginFill(boxBackgroundColor, 0.5);
    balanceBox.lineStyle(2, boxBorderColor);
    balanceBox.drawRect(0, 0, boxWidth, boxHeight);
    balanceBox.endFill();

    const winBox = new PIXI.Graphics();
    winBox.beginFill(boxBackgroundColor, 0.5);
    winBox.lineStyle(2, boxBorderColor);
    winBox.drawRect(0, 0, boxWidth, boxHeight);
    winBox.endFill();

    const betBox = new PIXI.Graphics();
    betBox.beginFill(boxBackgroundColor, 0.5);
    betBox.lineStyle(2, boxBorderColor);
    betBox.drawRect(0, 0, boxWidth, boxHeight);
    betBox.endFill();

    balanceBox.x = 0;
    balanceBox.y = app.screen.height - UI_AREA_HEIGHT;
    winBox.x = boxWidth;
    winBox.y = app.screen.height - UI_AREA_HEIGHT;
    betBox.x = boxWidth * 2;
    betBox.y = app.screen.height - UI_AREA_HEIGHT;

    app.stage.addChild(balanceBox, winBox, betBox);

    // Add balance, bet, win texts
    let balanceAmount = 10.0;
    const balanceText = new PIXI.Text(`BALANCE: ${balanceAmount.toFixed(2)}`, {
      fontFamily: "Arial",
      fontSize: 25,
      fill: 0xffffff,
      align: "center",
      fontWeight: "bold",
    });
    balanceText.anchor.set(0.5);
    balanceText.x = boxWidth / 2;
    balanceText.y = boxHeight / 2;

    balanceBox.addChild(balanceText);

    let winAmount = 0.0;
    const winText = new PIXI.Text(`WIN: ${winAmount.toFixed(2)}`, {
      fontFamily: "Arial",
      fontSize: 25,
      fill: 0xffffff,
      align: "center",
      fontWeight: "bold",
    });
    winText.anchor.set(0.5);
    winText.x = boxWidth / 2;
    winText.y = boxHeight / 2;

    winBox.addChild(winText);

    let betAmount = 0.01;
    const betText = new PIXI.Text(`BET: ${betAmount.toFixed(2)}`, {
      fontFamily: "Arial",
      fontSize: 25,
      fill: 0xffffff,
      align: "center",
      fontWeight: "bold",
    });
    betText.anchor.set(0.5);
    betText.x = boxWidth / 2;
    betText.y = boxHeight / 2;

    const downArrowDefaultTexture = PIXI.Texture.from("assets/down_arrow_default.png");
    const downArrowHoverTexture = PIXI.Texture.from("assets/down_arrow_hover.png");
    const downArrowPressedTexture = PIXI.Texture.from("assets/down_arrow_pressed.png");

    const upArrowDefaultTexture = PIXI.Texture.from("assets/up_arrow_default.png");
    const upArrowHoverTexture = PIXI.Texture.from("assets/up_arrow_hover.png");
    const upArrowPressedTexture = PIXI.Texture.from("assets/up_arrow_pressed.png");

    const downArrowIcon = new PIXI.Sprite(downArrowDefaultTexture);
    const upArrowIcon = new PIXI.Sprite(upArrowDefaultTexture);

    downArrowIcon.anchor.set(0.5);
    upArrowIcon.anchor.set(0.5);

    downArrowIcon.interactive = true;
    downArrowIcon.buttonMode = true;
    upArrowIcon.interactive = true;
    upArrowIcon.buttonMode = true;

    downArrowIcon.on("pointerover", () => {
      downArrowIcon.texture = downArrowHoverTexture;
      downArrowIcon.scale.set(0.9);
    });
    downArrowIcon.on("pointerout", () => {
      downArrowIcon.texture = downArrowDefaultTexture;
      downArrowIcon.scale.set(1.0);
    });
    downArrowIcon.on("pointerdown", () => {
      downArrowIcon.texture = downArrowPressedTexture;
      downArrowIcon.scale.set(0.9);
    });
    downArrowIcon.on("pointerup", () => {
      downArrowIcon.texture = downArrowHoverTexture;
      downArrowIcon.scale.set(0.9);
      decreaseBet();
    });
    downArrowIcon.on("pointerupoutside", () => {
      downArrowIcon.texture = downArrowDefaultTexture;
      downArrowIcon.scale.set(1.0);
    });

    upArrowIcon.on("pointerover", () => {
      upArrowIcon.texture = upArrowHoverTexture;
      upArrowIcon.scale.set(0.9);
    });
    upArrowIcon.on("pointerout", () => {
      upArrowIcon.texture = upArrowDefaultTexture;
      upArrowIcon.scale.set(1.0);
    });
    upArrowIcon.on("pointerdown", () => {
      upArrowIcon.texture = upArrowPressedTexture;
      upArrowIcon.scale.set(0.9);
    });
    upArrowIcon.on("pointerup", () => {
      upArrowIcon.texture = upArrowHoverTexture;
      upArrowIcon.scale.set(0.9);
      increaseBet();
    });
    upArrowIcon.on("pointerupoutside", () => {
      upArrowIcon.texture = upArrowDefaultTexture;
      upArrowIcon.scale.set(1.0);
    });

    const arrowSpacing = 40;
    downArrowIcon.x =
      betText.x - betText.width / 2 - arrowSpacing - downArrowIcon.width / 2;
    downArrowIcon.y = betText.y;

    upArrowIcon.x =
      betText.x + betText.width / 2 + arrowSpacing + upArrowIcon.width / 2;
    upArrowIcon.y = betText.y;

    betBox.addChild(downArrowIcon);
    betBox.addChild(betText);
    betBox.addChild(upArrowIcon);

    // Add icons to stage
    app.stage.addChild(...icons);
  } else {
    // Landscape mode positions
    const buttonsAreaX = REEL_WIDTH * 5 + REEL_WIDTH / 2 + 3;
    spinButton.x = buttonsAreaX;
    spinButton.y = (SYMBOL_SIZE * SYMBOLS_PER_REEL) / 2;

    buttonAbove.x = buttonsAreaX;
    buttonAbove.y =
      spinButton.y - spinButton.height / 2 - buttonAbove.height / 2 - 10;

    buttonBelow.x = buttonsAreaX;
    buttonBelow.y =
      spinButton.y + spinButton.height / 2 + buttonBelow.height / 2 + 10;

    app.stage.addChild(buttonAbove);
    app.stage.addChild(spinButton);
    app.stage.addChild(buttonBelow);

    // Adjust reel border
    const reelsBorder = new PIXI.Graphics();
    reelsBorder.lineStyle(12, 0xffd700, 1);
    reelsBorder.drawRoundedRect(
      reelBackground.x,
      reelBackground.y,
      REEL_WIDTH * 5,
      SYMBOL_SIZE * SYMBOLS_PER_REEL,
      20
    );
    app.stage.addChild(reelsBorder);

    // UI at the bottom
    const uiContainer = new PIXI.Container();
    uiContainer.y = app.screen.height - UI_AREA_HEIGHT;
    app.stage.addChild(uiContainer);

    const iconWidth = 70;
    const iconPadding = 10;
    const boxPadding = 150;
    const boxWidth = 300;
    const boxHeight = UI_AREA_HEIGHT;

    const totalUIWidth =
      iconWidth +
      iconPadding +
      (boxWidth * 3 + boxPadding * 2) +
      iconPadding +
      iconWidth;

    uiContainer.x = (app.screen.width - totalUIWidth) / 2 - 80;

    // Place music and settings icons
    musicNoteIcon.x = uiContainer.x;
    musicNoteIcon.y = boxHeight / 2;

    settingsIcon.x = app.screen.width - iconWidth - iconPadding;
    settingsIcon.y = boxHeight / 2;

    musicNoteIcon.interactive = true;
    musicNoteIcon.buttonMode = true;
    settingsIcon.interactive = true;
    settingsIcon.buttonMode = true;

    uiContainer.addChild(musicNoteIcon);
    uiContainer.addChild(settingsIcon);

    // Create balance, win, and bet boxes
    const boxBackgroundColor = 0x5a1d63;
    const boxBorderColor = 0x6a2d73;

    const balanceBox = new PIXI.Graphics();
    balanceBox.beginFill(boxBackgroundColor, 0.5);
    balanceBox.lineStyle(2, boxBorderColor);
    balanceBox.drawRoundedRect(0, 0, boxWidth, boxHeight, 5);
    balanceBox.endFill();

    const winBox = new PIXI.Graphics();
    winBox.beginFill(boxBackgroundColor, 0.5);
    winBox.lineStyle(2, boxBorderColor);
    winBox.drawRoundedRect(0, 0, boxWidth, boxHeight, 5);
    winBox.endFill();

    const betBox = new PIXI.Graphics();
    betBox.beginFill(boxBackgroundColor, 0.5);
    betBox.lineStyle(2, boxBorderColor);
    betBox.drawRoundedRect(0, 0, boxWidth, boxHeight, 5);
    betBox.endFill();

    balanceBox.x = iconWidth + iconPadding;
    winBox.x = balanceBox.x + boxWidth + boxPadding;
    betBox.x = winBox.x + boxWidth + boxPadding;

    uiContainer.addChild(balanceBox);
    uiContainer.addChild(winBox);
    uiContainer.addChild(betBox);

    // Add balance, bet, win texts
    let balanceAmount = 10.0;
    const balanceText = new PIXI.Text(`BALANCE: ${balanceAmount.toFixed(2)}`, {
      fontFamily: "Arial",
      fontSize: 25,
      fill: 0xffffff,
      align: "center",
      fontWeight: "bold",
    });
    balanceText.anchor.set(0.5);
    balanceText.x = balanceBox.x + boxWidth / 2;
    balanceText.y = boxHeight / 2;

    uiContainer.addChild(balanceText);

    let winAmount = 0.0;
    const winText = new PIXI.Text(`WIN: ${winAmount.toFixed(2)}`, {
      fontFamily: "Arial",
      fontSize: 25,
      fill: 0xffffff,
      align: "center",
      fontWeight: "bold",
    });
    winText.anchor.set(0.5);
    winText.x = winBox.x + boxWidth / 2;
    winText.y = boxHeight / 2;

    uiContainer.addChild(winText);

    let betAmount = 0.01;
    const betText = new PIXI.Text(`BET: ${betAmount.toFixed(2)}`, {
      fontFamily: "Arial",
      fontSize: 25,
      fill: 0xffffff,
      align: "center",
      fontWeight: "bold",
    });
    betText.anchor.set(0.5);
    betText.x = betBox.x + boxWidth / 2;
    betText.y = boxHeight / 2;

    uiContainer.addChild(betText);

    // Add up and down arrows for bet adjustment
    const downArrowDefaultTexture = PIXI.Texture.from("assets/down_arrow_default.png");
    const downArrowHoverTexture = PIXI.Texture.from("assets/down_arrow_hover.png");
    const downArrowPressedTexture = PIXI.Texture.from("assets/down_arrow_pressed.png");

    const upArrowDefaultTexture = PIXI.Texture.from("assets/up_arrow_default.png");
    const upArrowHoverTexture = PIXI.Texture.from("assets/up_arrow_hover.png");
    const upArrowPressedTexture = PIXI.Texture.from("assets/up_arrow_pressed.png");

    const downArrowIcon = new PIXI.Sprite(downArrowDefaultTexture);
    const upArrowIcon = new PIXI.Sprite(upArrowDefaultTexture);

    downArrowIcon.anchor.set(0.5);
    upArrowIcon.anchor.set(0.5);

    downArrowIcon.interactive = true;
    downArrowIcon.buttonMode = true;
    upArrowIcon.interactive = true;
    upArrowIcon.buttonMode = true;

    downArrowIcon.on("pointerover", () => {
      downArrowIcon.texture = downArrowHoverTexture;
      downArrowIcon.scale.set(0.9);
    });
    downArrowIcon.on("pointerout", () => {
      downArrowIcon.texture = downArrowDefaultTexture;
      downArrowIcon.scale.set(1.0);
    });
    downArrowIcon.on("pointerdown", () => {
      downArrowIcon.texture = downArrowPressedTexture;
      downArrowIcon.scale.set(0.9);
    });
    downArrowIcon.on("pointerup", () => {
      downArrowIcon.texture = downArrowHoverTexture;
      downArrowIcon.scale.set(0.9);
      decreaseBet();
    });
    downArrowIcon.on("pointerupoutside", () => {
      downArrowIcon.texture = downArrowDefaultTexture;
      downArrowIcon.scale.set(1.0);
    });

    upArrowIcon.on("pointerover", () => {
      upArrowIcon.texture = upArrowHoverTexture;
      upArrowIcon.scale.set(0.9);
    });
    upArrowIcon.on("pointerout", () => {
      upArrowIcon.texture = upArrowDefaultTexture;
      upArrowIcon.scale.set(1.0);
    });
    upArrowIcon.on("pointerdown", () => {
      upArrowIcon.texture = upArrowPressedTexture;
      upArrowIcon.scale.set(0.9);
    });
    upArrowIcon.on("pointerup", () => {
      upArrowIcon.texture = upArrowHoverTexture;
      upArrowIcon.scale.set(0.9);
      increaseBet();
    });
    upArrowIcon.on("pointerupoutside", () => {
      upArrowIcon.texture = upArrowDefaultTexture;
      upArrowIcon.scale.set(1.0);
    });

    const arrowSpacing = 40;
    downArrowIcon.x = betBox.x + boxWidth / 2 - betText.width / 2 - arrowSpacing - downArrowIcon.width / 2;
    downArrowIcon.y = betText.y;

    upArrowIcon.x = betBox.x + boxWidth / 2 + betText.width / 2 + arrowSpacing + upArrowIcon.width / 2;
    upArrowIcon.y = betText.y;

    uiContainer.addChild(downArrowIcon);
    uiContainer.addChild(upArrowIcon);
  }

  // Event handlers and game logic

  // Interactive elements
  spinButton.interactive = true;
  spinButton.buttonMode = true;
  settingsIcon.interactive = true;
  settingsIcon.buttonMode = true;
  musicNoteIcon.interactive = true;
  musicNoteIcon.buttonMode = true;
  aIcon.interactive = true;
  aIcon.buttonMode = true;
  boltIcon.interactive = true;
  boltIcon.buttonMode = true;
  buttonAbove.interactive = true;
  buttonAbove.buttonMode = true;
  buttonBelow.interactive = true;
  buttonBelow.buttonMode = true;

  // Implement interactive functionalities

  // Spin Button Event Handlers
  let running = false;
  let turboMode = false;
  let isStopping = false;

  spinButton.on("pointerover", onSpinButtonHoverOver);
  spinButton.on("pointerout", onSpinButtonHoverOut);
  spinButton.on("pointerdown", onSpinButtonDown);
  spinButton.on("pointerup", onSpinButtonUp);
  spinButton.on("pointerupoutside", onSpinButtonUp);

  function onSpinButtonHoverOver() {
    spinButton.scale.set(ICON_SCALE * 0.9);
    if (running) {
      spinButton.texture = spinButtonStopHoverTexture;
    } else {
      spinButton.texture = spinButtonHoverTexture;
    }
  }

  function onSpinButtonHoverOut() {
    spinButton.scale.set(ICON_SCALE);
    if (running) {
      spinButton.texture = spinButtonStopDefaultTexture;
    } else {
      spinButton.texture = spinButtonDefaultTexture;
    }
  }

  function onSpinButtonDown() {
    spinButton.scale.set(ICON_SCALE * 0.9);
    if (running) {
      spinButton.texture = spinButtonStopPressedTexture;
    } else {
      spinButton.texture = spinButtonPressedTexture;
    }
    onSpinButtonPressed();
  }

  function onSpinButtonUp() {
    spinButton.scale.set(ICON_SCALE * 0.9);
    if (running) {
      spinButton.texture = spinButtonStopHoverTexture;
    } else {
      spinButton.texture = spinButtonHoverTexture;
    }
  }

  function onSpinButtonPressed() {
    if (running) {
      if (!isStopping) {
        isStopping = true;
        stopReelsSmoothly();
      }
      return;
    }
    spinButton.texture = spinButtonStopDefaultTexture;
    startPlay();
  }

  function stopReelsSmoothly() {
    for (let i = 0; i < reels.length; i++) {
      const r = reels[i];
      let reelTween = tweening.find(
        (t) => t.object === r && t.property === "position"
      );

      if (reelTween) {
        const currentPos = r.position;
        const target = Math.ceil(currentPos);

        reelTween.propertyBeginValue = currentPos;
        reelTween.target = target;
        reelTween.time = 500 + i * 100;
        reelTween.start = Date.now();
        reelTween.easing = backout(0.5);
        reelTween.change = null;
        reelTween.complete = i === reels.length - 1 ? reelsComplete : null;
      } else {
        const currentPos = r.position;
        const target = Math.ceil(currentPos);

        const time = 500 + i * 100;
        tweenTo(
          r,
          "position",
          target,
          time,
          backout(0.5),
          null,
          i === reels.length - 1 ? reelsComplete : null
        );
      }
    }
  }

  function reelsComplete() {
    running = false;
    isStopping = false;
    spinButton.texture = spinButtonDefaultTexture;

    for (let i = 0; i < reels.length; i++) {
      const r = reels[i];
      r.position = Math.round(r.position);
      r.previousPosition = r.position;
    }
  }

  function startPlay() {
    if (running) return;
    running = true;
    isStopping = false;

    for (let i = 0; i < reels.length; i++) {
      const r = reels[i];
      const extra = Math.floor(Math.random() * 3);
      let target, time;
      if (turboMode) {
        target = r.position + 10 + extra;
        time = 1000 + extra * 100;
      } else {
        target = r.position + 10 + i * 5 + extra;
        time = 2500 + i * 600 + extra * 600;
      }
      tweenTo(
        r,
        "position",
        target,
        time,
        backout(0.5),
        null,
        i === reels.length - 1 ? reelsComplete : null
      );
    }
  }

  // Increase and decrease bet functions
  function increaseBet() {
    betAmount *= 2;
    updateBetText();
  }

  function decreaseBet() {
    betAmount /= 2;
    if (betAmount < 0.01) {
      betAmount = 0.01;
    }
    updateBetText();
  }

  function updateBetText() {
    betText.text = `BET: ${betAmount.toFixed(2)}`;
  }

  // Animation loop
  app.ticker.add(() => {
    for (let i = 0; i < reels.length; i++) {
      const r = reels[i];
      r.blur.blurY = (r.position - r.previousPosition) * 8;
      r.previousPosition = r.position;

      for (let j = 0; j < r.symbols.length; j++) {
        const s = r.symbols[j];
        const prevY = s.y;
        s.y = ((r.position + j) % r.symbols.length) * SYMBOL_SIZE - SYMBOL_SIZE;
        if (s.y < 0 && prevY > SYMBOL_SIZE) {
          s.texture =
            slotTextures[Math.floor(Math.random() * slotTextures.length)];
          s.scale.x = s.scale.y = Math.min(
            SYMBOL_SIZE / s.texture.width,
            SYMBOL_SIZE / s.texture.height
          );
          s.x = Math.round((REEL_WIDTH - s.width) / 2);
        }
      }
    }
  });

  // Tweening utility
  const tweening = [];

  function tweenTo(
    object,
    property,
    target,
    time,
    easing,
    onchange,
    oncomplete
  ) {
    const tween = {
      object,
      property,
      propertyBeginValue: object[property],
      target,
      easing,
      time,
      change: onchange,
      complete: oncomplete,
      start: Date.now(),
    };

    tweening.push(tween);
    return tween;
  }

  app.ticker.add(() => {
    const now = Date.now();
    const remove = [];
    for (let i = 0; i < tweening.length; i++) {
      const t = tweening[i];
      const phase = Math.min(1, (now - t.start) / t.time);

      t.object[t.property] = lerp(
        t.propertyBeginValue,
        t.target,
        t.easing(phase)
      );
      if (t.change) t.change(t);

      if (phase === 1) {
        t.object[t.property] = t.target;
        if (t.complete) t.complete(t);
        remove.push(t);
      }
    }
    for (let i = 0; i < remove.length; i++) {
      tweening.splice(tweening.indexOf(remove[i]), 1);
    }
  });

  function lerp(a1, a2, t) {
    return a1 * (1 - t) + a2 * t;
  }

  function backout(amount) {
    return function (t) {
      return --t * t * ((amount + 1) * t + amount) + 1;
    };
  }

  // Adjust for window resize
  window.addEventListener("resize", () => {
    location.reload();
  });
}
